import logo from '../../Naomi-logo.svg'

const Footer = () => {
    return(
        <footer>
            <section>
                <div style={{ padding: "1em",display: "flex", flexDirection: 'column', alignItems: 'center', backgroundColor: "#243746"}}>
                    <img alt="Naomi For City Council" style={{margin: "1em"}} src={logo} />
                    <p style={{color: 'white'}} >
                        Use your own image only. No information collected in the use of this website will be used by the campaign or shared with any party.</p>
                        <a style={{color: 'white' }} href="https://www.naomiforcitycouncil.com/">Paid for by Naomi For City Council</a>                     
                        <p style={{color: 'white'}} >Questions? Contact Team@NaomiForCityCouncil.com</p>
                    <p style={{color: 'white'}} >Created by Cade W. Design and <a style={{color: 'white' }} href="https://github.com/danwood/iendorsebernie">open-source software</a> credit to <a style={{color: 'white' }} href="https://twitter.com/danwood">Dan Wood</a></p>
                </div>
            </section>
        </footer>
    )
}

export default Footer;