import styled from 'styled-components'

export const Form = styled.form`
    display: block;
    width: 300px;
    margin: 50px auto;
`;

export const Label = styled.label`
    margin-bottom: 0.5em;
    display: block;
`;

export const Input = styled.input`
	padding: 0.5em;
	border-radius: 3px;
	width: 100%;
	margin-bottom: 0.5em;
	border-color: black ;
	&:focus {
		outline: 2px solid #007CBA;
	}
	outline: ${props => props.required ? "2px solid red" : "none"};

	&:hover{
		background-color: lightgrey;
	}
`;
export const TextArea = styled.textarea`
	padding: 0.5em;
	border-radius: 3px;
	width: 100%;
	margin-bottom: 0.5em;
	border-color: black ;
	color: black;
	outline: ${props => props.required ? "2px solid red" : "none"};
	&:focus {
		outline: 2px solid #007CBA;
	}
	&:hover{
		background-color: lightgrey;
	}
`;

export const ImageUploadWrapper = styled.div`

`

export const ImagePreview = styled.img`
padding: 0.5em;
border-radius: 3px;
border-color: black ;
width: 75%;
margin-bottom: 0.5em;
`