/* eslint-disable no-mixed-operators */
import React, { useEffect, useRef } from 'react'
import { CanvasWrapper, DownloadButton } from './Canvas.style'

const Canvas = (props) => {
    const {firstName, lastName, position, reason, image, logo} = props;
    const canvasRef = useRef(null)
    
    const drawRectangle = (ctx) => {
        ctx.fillStyle = '#3BBFAD';
        ctx.fillRect(0,0,1200,630);
        createBorder(ctx, 0, 0, 1200, 630, 9)
    }

    // https://stackoverflow.com/questions/38173871/html5-canvas-how-to-border-a-fillrect
    const createBorder = (ctx,x, y, w, h, thickness, fill = 'white', image = false ) => {
        ctx.fillStyle = fill;
        if (image){
            ctx.rect(x + (thickness), y + (thickness), w - (thickness * 2), h - (thickness * 2));
        } else {
            ctx.fillRect(x + (thickness), y + (thickness), w - (thickness * 2), h - (thickness * 2));
        }
    }

    const drawReason = (ctx) => {
        ctx.fillStyle = 'rgba(59,191,173,0.3)';
        ctx.fillRect(373, 132, 793, 319);
    }

    const drawReasonText = (ctx) => {
        const reasonText = `"${reason}"`;
        const margin = 50;
        const maxSize = 40;

        const reasonBoxWidth = 793;
        const reasonBoxHeight = 319;
        const reasonBoxMidX = 373 + reasonBoxWidth / 2;
        const reasonBoxMidY = 132 + reasonBoxHeight / 2;
       
        ctx.fillStyle = 'black';
        ctx.font = "400 " + String(32) + "px Open Sans, sans-serif";
        ctx.textAlign = "center";
       
        var words = reasonText.split(" ");
        var currentLine = words[0];
    
        var measurement = ctx.measureText(reasonText).width;

        //Calculate size and starting y position based on message length
        var size = maxSize + (-1.5 * (measurement / (reasonBoxWidth - margin)))
        var currentY = (reasonBoxMidY - 20) + (-20 * (measurement / (reasonBoxWidth - margin)))
       
        for (var i = 1; i < words.length; i++) {
            var word = words[i];
            var width = ctx.measureText(currentLine + " " + word).width;
            ctx.font = "400 " + String(size) + "px Open Sans, sans-serif";
            
            if (width < reasonBoxWidth - margin) {
                currentLine += " " + word;
            } else {
                currentY += 40;
                ctx.fillText(currentLine, reasonBoxMidX, currentY);
                currentLine = word;
            }
        }
        currentY+=40;

        ctx.fillText(currentLine, reasonBoxMidX, currentY);
    }

    const drawAvatar = (ctx, img) => {
        //https://stackoverflow.com/questions/27504492/get-center-of-a-square-based-on-the-4-corners
        const squareX = 44;
        // Same as Reason Box
        const squareY = 132;

        const squareWidth = 285;
        // Same as Reason Box
        const squareHeight = 319;

        const midpointX = squareX + squareWidth / 2;
        const midpointY = squareY + squareHeight / 2;
    
        //https://github.com/danwood/iendorsebernie/blob/ninaturner/js/main.js#L283

        // Draw green border square
        ctx.fillRect(44, 132, squareWidth, squareHeight);

        const frameAspect = squareWidth / squareHeight;
        
        ctx.save();
        ctx.beginPath();
        // Create Image Square
        createBorder(ctx, squareX, squareY, squareWidth, squareHeight, 9,  'rgba(59,191,173,0.3)', true)
        ctx.clip();

        var naturalWidth = img.naturalWidth;
        var naturalHeight = img.naturalHeight;
        var aspect = naturalWidth / naturalHeight;

        if (aspect > frameAspect) {	
            ctx.drawImage(img, midpointX-(squareHeight*aspect)/2, midpointY-squareHeight/2, squareHeight*aspect, squareHeight);
        }
        else {
            ctx.drawImage(img, midpointX-squareWidth/2, midpointY-(squareWidth/aspect)/2, squareWidth, squareWidth/aspect);
        }
        ctx.restore();
    }

    const drawLogo = (ctx, logo) => {
        ctx.drawImage(logo, 44, 466, 285, 124);
    }

    const drawHeadline = (ctx) => {
        // Adapted from https://github.com/danwood/iendorsebernie/blob/4a53172175af451f2de93e80f304daa56b1e95e4/js/main.js#L369
        const headline =  firstName + " Endorses Naomi Hattaway For City Council";
        const margin = 50;
        const maxSize = 50;
        const minSize = 12;

        for(var size = maxSize ; size > minSize ; size -= 1) {
            ctx.font = "600 " + String(size) + "px Open Sans, sans-serif";
            var textWidth = ctx.measureText(headline).width + margin;
            if (textWidth <= 1200 - margin) {
                break;
            }
        }

        ctx.fillStyle = 'black';
        ctx.font = "600 " + String(size) + "px Open Sans, sans-serif";
        ctx.textAlign = "left";
        ctx.fillText(headline, 44, 80);
    }

    const drawEndorsement = (ctx) => {
        drawFullName(ctx);
        drawPosition(ctx)

        ctx.fillStyle = '#D05559';
        ctx.fillRect(375, 506, 694, 7.5);
    }

    const drawFullName = (ctx) => {
        var fullName = `${firstName} ${lastName}`;
        const maxSize = 24;
        const minSize = 12;

        for(var size = maxSize ; size > minSize ; size -= 1) {
            ctx.font = "700 " + String(size) + "px Open Sans, sans-serif";
            var textWidth = ctx.measureText(fullName).width;
            if (textWidth <= 694) {
                break;
            }
        }
        ctx.font = "700 " + String(size) + "px Open Sans, sans-serif";
        ctx.textAlign = "left";
        ctx.fillText(fullName, 376, 500);
    }

    const drawPosition = (ctx) => {
        const maxSize = 20;
        const minSize = 12;

        for(var size = maxSize ; size > minSize ; size -= 1) {
            ctx.font = "300 " + String(size) + "px Open Sans, sans-serif";
            var textWidth = ctx.measureText(position).width;
            if (textWidth <= 694) {
                break;
            }
        }
        ctx.font = "300 " + String(size) + "px Open Sans, sans-serif";
        ctx.textAlign = "left";
        ctx.fillText(position, 376, 533);
    }

    const drawTopBar = (ctx) => {
        ctx.fillStyle = "#D05559";
        ctx.fillRect(375, 112, 790, 7.5);
    }

    const downloadImage = (link) => {
        const image = canvasRef.current.toDataURL("image/png").replace("image/png", "image/octet-stream")
        link.download = "I_Endorse_Naomi.png";
        link.href = image;
    }

    useEffect(() => {
        const canvas = canvasRef.current
        const context = canvas.getContext('2d')
        var avatarImage = new Image();
        var logoImage = new Image();
        logoImage.onload = function(){}
        logoImage.src = logo;
        avatarImage.onload = function(){
            render()
        }
        const render = () => {
            drawRectangle(context)
            drawReason(context)
            drawAvatar(context, avatarImage)
            drawLogo(context,logoImage)
            drawHeadline(context)
            drawReasonText(context)
            drawEndorsement(context)
            drawTopBar(context)
        }
        avatarImage.onerror=function(){
			alert("Image didn't upload, please try again!");
		} 
        avatarImage.src = image;
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [image, logo, props])
   
    
    return (
        <CanvasWrapper >
        <canvas id="endorsementCanvas" aria-label="EndorsementImage" style={{ margin: 'auto', width: "75%"}} width="1200" height="630" ref={canvasRef}/>
        <DownloadButton href="#" onClick={(e) => downloadImage(e.target)}style={{ margin: '2em auto', width: "25%"}}>Download</DownloadButton>
        </CanvasWrapper>
    )
}

export default Canvas;