import React from 'react'
import { Form, Input, Label, TextArea, ImagePreview } from './InputForm.style';
import Canvas from '../Canvas/Canvas'
import logo from '../../Naomi-logo.svg'
class InputForm extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
          firstName: '',
          lastName: '',
          position: '',
          reason: '',
          image: null,
          logo: logo,
          attempted: false,
          submitted: false,
        };
      this.handleChange = this.handleChange.bind(this);
      this.handleFileInput = this.handleFileInput.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);

    }
    
    handleChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
        this.setState({[name]: value});
    }

    handleFileInput(input) {
        const target = input.target;
        const files = target.files;
        
        if (input.target.files && input.target.files[0]){
            const img = input.target.files[0];
            this.setState({
                image: URL.createObjectURL(img)
            })
        }

        var reader = new FileReader();
        reader.readAsDataURL(files[0])
    }
     handleSubmit(event) {
        this.setState({attempted: true})
        if (this.state.image === null){
            alert("Please upload a photo!");
        } else if (this.state.reason === '') {
            alert("Please fill out all of the fields!");
        }
        else{
            this.setState({submitted: true})
        }
        event.preventDefault();
    }
    
    render() {
      return (
          <>
        <Form  onSubmit={this.handleSubmit}>
            <Label htmlFor="firstName">First Name:</Label>
            <Input required={this.state.attempted && !this.state.firstName} type="text" name="firstName" id="firstName" value={this.state.firstName} onChange={this.handleChange} />

            <Label htmlFor="lastName">Last Name:</Label>
            <Input type="text" name="lastName" id="lastName" value={this.state.lastName} onChange={this.handleChange} />

            <Label htmlFor="position">Position/Title:</Label>
            <Input type="text" name="position" id="position" value={this.state.position} onChange={this.handleChange} />

            <Label htmlFor="reason">Why Do You Support Naomi Hattaway for City Council? </Label>
            <TextArea required={this.state.attempted && !this.state.reason} type="text" placeholder="Enter a reason!" name="reason" id="reason" value={this.state.reason} rows={6} onChange={this.handleChange} />

            <Label htmlFor="image">Upload Image</Label>
            <Input tabindex={0} required={this.state.attempted && !this.state.image} type="file" value="" name="image" id="image" onChange={this.handleFileInput} />
            <ImagePreview alt="Upload a profile photo!" id="avatarImage" src={this.state.image} />

            <Input type="submit" value="Generate!" />
          </Form>
          {this.state.submitted && <Canvas {...this.state} />}
          
          </>
      );
    }
  }

  
  export default InputForm;